import React, { lazy } from 'react'
import ReactDOM from 'react-dom/client'
import { ChakraProvider, extendTheme } from '@chakra-ui/react'
import { Provider as ReduxProvider } from 'react-redux'
import { colors } from './styles'
import store from './state'

const App = lazy(() => import('./App'))
const App2 = lazy(() => import('./App2'))
const App3 = lazy(() => import('./App3'))

const breakpoints = {
  sm: '576px',
  md: '852px',
  lg: '968px',
  xl: '1080px',
  '2xl': '1200px',
}

const theme = extendTheme({
  styles: {
    global: {
      'html, body': {
        fontFamily: 'Poppins, sans-serif',
        borderColor: 'red',
        background: 'transparent',
      },
    },
  },
  colors,
  breakpoints,
  components: {
    Progress: {
      baseStyle: {
        filledTrack: {
          bg: 'foreground',
        },
      },
    },
  },
})

const Provider = ({ children }) => {
  return (
    <ReduxProvider store={store}>
      <ChakraProvider theme={theme}>{children}</ChakraProvider>{' '}
    </ReduxProvider>
  )
}

window.renderFeatured = function () {
  const root = ReactDOM.createRoot(document.getElementById('featured-collections'))
  root.render(
    <React.StrictMode>
      <Provider>
        <App />
      </Provider>
    </React.StrictMode>,
  )
}

window.renderStats = function () {
  const root = ReactDOM.createRoot(document.getElementById('stats'))
  root.render(
    <React.StrictMode>
      <Provider>
        <App2 />
      </Provider>
    </React.StrictMode>,
  )
}

export const renderCollection = () => {
  const ele = document.createElement('div')
  ele.id = 'collection-view'
  document.body.appendChild(ele)
  const root2 = ReactDOM.createRoot(ele)
  root2.render(
    <React.StrictMode>
      <Provider>
        <App3 />
      </Provider>
    </React.StrictMode>,
  )
}

renderCollection()
